$laptopWidth: 992px;
$mobileWidth: 576px;

.title {
	font-family: "Exo";
	color: $textMainColor;

	&--large {
		font-size: 6.4rem;
		line-height: 7.36rem;
		font-weight: 600;
	}
	&--medium {
		font-size: 5rem;
		line-height: 5.75rem;
		font-weight: 500;
	}
	&--small {
		font-size: 3.2rem;
		line-height: 4.56rem;
		font-weight: 400;
	}

	&--light {
		color: $textContrastColor;
	}

	&--404 {
		font-size: 40rem;
		line-height: 40.8rem;
		color: #fedb1d;
	}
	@media screen and (max-width: $mobileWidth) {
		&--large {
			font-size: 3.6rem;
			line-height: 4.3rem;
		}
		&--medium {
			font-size: 3.2rem;
			line-height: 3.68rem;
		}
		&--small {
			font-size: 2.4rem;
			line-height: 2.5rem;
		}
		// 	&--404 {
		// 		font-size: 17.4rem;
		// 		line-height: 18rem;
		// 	}
	}
}
