$laptopWidth: 992px;
$mobileWidth: 576px;

.content {
	font-family: "Proxima";
	font-weight: 400;
	font-size: 1.8rem;
	line-height: 2.43rem;
	color: $textMainColor;
	text-decoration: none;

	&--large {
		font-size: 2.4rem;
		line-height: 3.42rem;
	}

	&--small {
		font-size: 1.8rem;
		line-height: 2.7rem;
	}

	&--light {
		color: $textContrastColor;
	}
	@media screen and (max-width: $mobileWidth) {
		font-size: 1.4rem;
		line-height: 1.7rem;
		&--large {
			font-size: 2rem;
			line-height: 2.5rem;
		}
	}
}
