.menu {
  $root: &;
  position: relative;
  &__list {
    display: flex;
    list-style: none;
    padding: 0;
  }

  &__item {
    margin-left: 5.6rem;
  }

  &__link {
    @extend .content;
    @extend .content--light;
    &:hover {
      text-decoration: underline;
    }
  }
  &__button {
    width: 6rem;
    height: 5rem;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    display: none;
    margin-left: auto;
    .icon {
      width: 100%;
      max-width: 100%;
      height: 100%;
    }
  }
  @media screen and (max-width: $laptopWidth) {
    overflow: hidden;
    &--open {
      overflow: visible;
    }
    &__list {
      display: flex;
      // opacity: 0;
      flex-direction: column;
      width: 100%;
      position: absolute;
      top: 5.2rem;
      right: -6.3rem;
      background-color: $backgroundMainColor;
      z-index: 2;
      // overflow: hidden;

      transform: translateX(100%);
      transition: 0.5s transform linear;
    }

    &--open #{$root}__list {
      // opacity: 1;
      transform: translateX(0);
    }
    &__item {
      border-bottom: 0.3rem solid $white;
      margin: 0;
    }
    &__link {
      display: block;
      padding-top: 1.5rem;
      padding-bottom: 1.5rem;
      padding-left: 1.5rem;
      width: 100%;
      height: 100%;
      transition: 0.3s all linear;
      &:hover {
        text-decoration: none;
        color: $backgroundMainColor;
        background-color: $white;
      }
    }
    &__button {
      display: flex;
    }
  }
  @media screen and (max-width: $mobileWidth) {
    &__list {
      height: 100vh;
      right: -0.2rem;
    }
    &__button {
      width: 4.7rem;
      height: 4.1rem;
    }
  }
}
