.button {
  position: relative;
  padding: 1.2rem 2.5rem 1.3rem;
  font-family: "Exo";
  font-size: 2rem;
  line-height: 2.4rem;
  font-weight: 500;
  text-align: center;
  color: $white;
  cursor: pointer;
  border: 0.1rem solid $white;
  background-color: transparent;
  transition: 0.3s all linear;
  text-decoration: none;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 70%;
    height: 100%;
    background: linear-gradient(92.73deg, rgba(219, 0, 255, 0.8) 28.98%, rgba(0, 41, 255, 0.8) 80.99%);
    filter: blur(4.2rem);
    z-index: -1;
    opacity: 0;
    // transition: 0.2s opacity linear;
  }
  &:hover {
    background-color: $white;
    color: $black;
    &::before {
      opacity: 1;
    }
  }
  &--light {
    background-color: $white;
    color: $black;
    &:hover {
      color: $white;
      background-color: $textAccentColor;
    }
  }

  &--dark {
    border-color: $black;
    background-color: $black;
    color: $white;
    &:hover {
      background-color: $white;
      color: $black;
      &::before {
        opacity: 0;
      }
    }
  }
  @media screen and (max-width: $mobileWidth) {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
}
