@import "vars";
@import "mixins";
@import "vendor";
@import "settings";
@import "fonts";
@import "typography";
@import "button";
@import "menu";
@import "animations";

.header {
  border-bottom: 0.1rem solid $white;
  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__logo {
    @extend .content;
    @extend .content--large;
    display: block;
    margin-right: auto;

    padding-top: 1.9rem;
    padding-bottom: 1.9rem;
  }

  &__menu {
    width: 76%;
  }

  @media screen and (max-width: $laptopWidth) {
    border-bottom: none;
    padding-top: 0.7rem;
  }

  @media screen and (max-width: $mobileWidth) {
    &__menu {
      width: 100%;
    }
  }
}

.logo {
  &__link {
    color: $white;
    text-decoration: none;
  }

  // &__image {
  //   width: 100%;
  //   max-width: 100%;
  //   height: 100%;
  // }
}

.promo {
  position: relative;
  overflow: hidden;
  padding-bottom: 7.5rem;

  &__content-wrapper {
    border-right: 0.3rem solid $white;
  }
  &__content {
    margin-top: 5.3rem;
    margin-left: auto;
    margin-right: 9.7rem;
    width: 74%;
    &::before,
    &::after {
      content: "";
      display: block;
      width: 100%;
      border: 0.3rem solid $white;
    }
    &::before {
      border-bottom: none;
      height: 5.7rem;
    }
    &::after {
      border-top: none;
      height: 10rem;
    }
  }

  &__content-container {
    transform: translateX(-19.4rem);
    width: 58.5rem;
    padding-top: 2.1rem;
    padding-bottom: 5.1rem;
  }

  &__title {
    padding-bottom: 2.1rem;
  }

  &__subtitle {
    padding-bottom: 2rem;
    letter-spacing: 0.02rem;
  }

  &__button {
    margin-top: 3.4rem;
    width: 35.2rem;
  }

  &__image-wrapper {
    position: absolute;
    right: 0;
    top: 2rem;
    z-index: -1;
    width: 57.2rem;
  }

  &__image {
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__cards {
    display: flex;
    justify-content: flex-start;
    margin-top: 10.1rem;
  }

  &__card {
    width: 33%;
    &:not(:nth-child(3n + 1)) {
      border-left: 0.1rem solid $white;
    }
    &:nth-child(3n + 1) {
      padding-left: 0;
    }
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 40.7rem;
    height: 40.7rem;
    border-radius: 50%;
    filter: blur(25.2rem);
  }
  &::before {
    background: rgba(0, 41, 255, 0.4);
    left: -21rem;
    bottom: 13rem;
  }
  &::after {
    background: linear-gradient(164.54deg, rgba(219, 0, 255, 0.4) -6.16%, rgba(255, 0, 0, 0.352) 42.53%);
    right: -12%;
    bottom: -37%;
  }
  @media screen and (max-width: $laptopWidth) {
    padding-top: 17.1rem;
    padding-bottom: 10.6rem;
    &::before {
      bottom: auto;
      top: 10rem;
    }
    &__content-container {
      width: 95%;
      transform: translateY(-31.2rem);
    }
    &__content {
      width: 100%;
      &::before {
        width: 2.3rem;
        height: 9.2rem;
        transform: translateY(4.7rem);
        margin-left: auto;
        border-left: none;
      }
      &::after {
        height: 24.8rem;
        margin-top: -33rem;
      }
    }
    &__content-wrapper {
      border-right: none;
    }

    &__button {
      width: 32rem;
    }
    &__image-wrapper {
      width: 46rem;
      height: 35rem;
      bottom: 29%;
      top: auto;
      right: 50%;
      transform: translateX(50%);
    }
    &__cards {
      margin-top: 22.5rem;
    }
  }

  @media screen and (max-width: $mobileWidth) {
    padding-top: 0;
    padding-bottom: 4rem;

    &__content-container {
      width: 100%;
      transform: none;
      padding-bottom: 3.2rem;
    }
    &__content {
      width: 100%;
      margin-top: 0;
      &::before {
        display: none;
      }
      &::after {
        display: none;
      }
    }
    &__content-container {
      padding-top: 0.4rem;
    }
    &__button {
      width: 100%;
      margin-top: 1.1rem;
    }
    &__image-wrapper {
      width: 100%;
      position: static;
      transform: none;
      height: 22.4rem;
    }
    &__cards {
      flex-direction: column;
      margin-top: 3.5rem;
    }
    &__card {
      width: 100%;
      padding-left: 0;
      margin-bottom: 1.5rem;
      &:not(:nth-child(3n + 1)) {
        border-left: none;
      }
      &:nth-child(3n + 1) {
        padding-left: 0;
      }
    }
  }
}

.card-header {
  padding-left: 3rem;
  &__top {
    @extend .title, .title--small, .title--light;
    &--large {
      font-size: 6rem;
      line-height: 5.9rem;
    }
  }

  &__bottom {
    @extend .content, .content--large, .content--light;
    padding-top: 0.8rem;
  }
  @media screen and (max-width: $laptopWidth) {
    &__bottom {
      font-size: 1.8rem;
      line-height: 2.4rem;
    }
  }
  @media screen and (max-width: $mobileWidth) {
    padding-left: 0;
    &__top {
      &--large {
        font-size: 4rem;
        line-height: 4.6rem;
      }
    }
    &__bottom {
      font-size: 1.6rem;
      line-height: 2.16rem;
    }
  }
}

.about {
  $root: &;
  padding-top: 8.3rem;
  padding-bottom: 2.2rem;
  background-color: $backgroundSubColor;
  &__top {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
  }

  &__content {
    width: 54.8rem;
  }

  &__title {
    @extend .title, .title--medium;
    padding-bottom: 2rem;
  }

  &__text {
    @extend .content;
  }

  &__image-wrapper {
    position: relative;
    width: 48.2rem;
  }

  &__star {
    position: absolute;
    top: -5.6rem;
    right: -6.7rem;
    width: 13.1rem;
    height: 13.1rem;
    color: #0029ff;
  }

  &__image {
    width: 100%;
    min-width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
  }

  &__cards {
    display: flex;
    margin-top: 4.1rem;
  }

  &__card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 25%;
    margin-bottom: 4rem;
    padding-right: 1.8rem;
    padding-left: 1rem;
    border-top: 1.7rem solid transparent;
    border-bottom: 1.7rem solid transparent;
    transition: 0.3s all linear;
    &:not(:nth-child(4n + 1)) {
      border-left: 0.1rem solid $black;
      padding-left: 2.4rem;
    }
    &:hover {
      background-color: $backgroundMainColor;
      & #{$root}__icon {
        color: $textContrastColor;
      }
      & #{$root}__card-text {
        color: $textContrastColor;
      }
    }
  }

  &__icon {
    width: 6.2rem;
    height: 6.2rem;
    color: $textMainColor;
  }

  &__card-text {
    @extend .content;
    padding-top: 1.9rem;
  }

  @media screen and (max-width: $laptopWidth) {
    padding-top: 5.5rem;
    padding-bottom: 2.9rem;
    overflow: hidden;
    &__top {
      flex-direction: column;
    }
    &__image-wrapper {
      width: 100%;
      margin-top: 5.5rem;
      height: 36.5rem;
    }
    &__cards {
      margin-top: 3.4rem;
      flex-wrap: wrap;
    }
    &__card {
      width: 50%;
      margin-bottom: 1.2rem;
      &:nth-child(2n + 1) {
        padding-left: 1rem;
        border-left: none;
      }
    }
    &__icon {
      width: 6rem;
      height: 6rem;
    }
  }

  @media screen and (max-width: $mobileWidth) {
    padding-top: 4.5rem;
    &__content {
      width: 100%;
    }
    &__text {
      margin-bottom: 1.6rem;
    }
    &__image-wrapper {
      width: 100%;
      margin-top: 2.7rem;
      height: 19.3rem;
    }
    &__star {
      display: none;
    }
    &__cards {
      margin-top: 2.1rem;
    }
    &__card {
      width: 100%;
      flex-direction: row;
      padding: 0;
      pointer-events: none;
      align-items: center;
      margin-bottom: 0.6rem;

      &:nth-child(2n + 1) {
        padding-left: 0;
      }
      &:not(:nth-child(4n + 1)) {
        border-left: none;
        padding-left: 0;
      }
    }
    &__icon {
      width: 4.8rem;
      height: 4.8rem;
      min-width: 4.8rem;
      margin-right: 1.9rem;
    }
    &__card-text {
      padding-top: 0;
    }
  }
}

.servises {
  position: relative;
  overflow: hidden;
  padding-top: 4.9rem;
  &::before {
    content: "";
    display: block;
    width: 50.6rem;
    height: 50.6rem;
    border-radius: 50%;
    background: linear-gradient(184.13deg, rgba(0, 41, 255, 0.7) 20.95%, rgba(255, 0, 0, 0.7) 96.63%);
    filter: blur(32.8rem);
    position: absolute;
    top: -9.7rem;
    right: -16.7rem;
    z-index: -1;
  }
  &__top {
    position: relative;
  }

  &__title {
    @extend .title, .title--medium, .title--light;
  }

  &__content {
    @extend .content, .content--light;
    width: 65%;
    padding-top: 2rem;
  }

  &__discount-wrapper {
    position: absolute;
    right: -0.9rem;
    top: 5.9rem;
    width: 18rem;
    height: 18rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__discount {
    @extend .content, .content--small, .content--light;
    width: 10.5rem;
    text-align: center;
    line-height: 1.6rem;
  }

  &__discount-icon {
    width: 100%;
    height: 100%;
    color: $white;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__discount-value {
    @extend .title, .title--small, .title--light;
    margin-bottom: -0.8rem;
    margin-top: -0.8rem;
    display: block;
  }

  &__cards {
    width: 100%;
    padding-top: 6rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-bottom: 10.7rem;
    border-bottom: 0.1rem solid $white;
  }

  &__card {
    width: 35.5rem;
    margin-bottom: -5rem;
    &:not(:nth-child(3n)) {
      margin-right: 3rem;
    }
    &:nth-child(3n + 2) {
      margin-top: 6rem;
    }
    &:nth-child(3n + 3) {
      margin-top: 13.5rem;
    }
  }
  @media screen and (max-width: $laptopWidth) {
    padding-top: 6rem;

    &::before {
      width: 39.1rem;
      height: 39.1rem;
      filter: blur(22.8rem);
    }

    &__discount-wrapper {
      width: 15.8rem;
      height: 15.8rem;
      top: 0;
      right: 0;
    }
    &__discount-value {
      font-size: 2.8rem;
    }
    &__discount {
      font-size: 1.6rem;
    }
    &__card {
      width: 29.9rem;
      &:not(:nth-child(3n)) {
        margin-right: 0;
      }
      margin-right: 2rem;
      margin-bottom: 3.6rem;
      &:nth-child(3n + 2) {
        margin-top: 0rem;
      }
      &:nth-child(3n + 3) {
        margin-top: 0rem;
      }
      &:nth-child(2n) {
        margin-right: 0;
        margin-top: 6.8rem;
      }
    }
    &__cards {
      padding-bottom: 4.2rem;
    }
  }
  @media screen and (max-width: $mobileWidth) {
    padding-top: 4.2rem;
    &__content {
      width: 63%;
    }
    &__discount-wrapper {
      width: 10.6rem;
      height: 10.6rem;
      top: 1rem;
    }
    &__discount-value {
      font-size: 2rem;
      padding-bottom: 0.5rem;
    }
    &__discount {
      font-size: 1.4rem;
    }
    &__cards {
      padding-top: 5.5rem;
      padding-bottom: 0.9rem;
    }
    &__card {
      width: 100%;
      margin-right: 0;
      margin-bottom: 5.2rem;
      &:nth-child(2n) {
        margin-top: 0;
      }
    }
  }
}

.card {
  &__image-wrapper {
    width: 100%;
  }

  &__image {
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__title {
    @extend .content, .content--large, .content--light;
    padding-top: 2rem;
  }

  &__content {
    @extend .title, .title--small, .title--light;
  }

  &__button {
    width: 100%;
    margin-top: 1.8rem;
  }
  @media screen and (max-width: $laptopWidth) {
    &__image-wrapper {
      height: 40.5rem;
    }
  }
  @media screen and (max-width: $mobileWidth) {
    &__image-wrapper {
      height: 39.5rem;
    }
    &__title {
      padding-top: 2.7rem;
    }
    &__content {
      padding-top: 0.6rem;
    }
    &__button {
      margin-top: 2.9rem;
      font-size: 2.4rem;
    }
  }
}

.advantages {
  position: relative;
  padding-top: 6.3rem;
  padding-bottom: 2.8rem;

  &::before {
    content: "";
    display: block;
    width: 61rem;
    height: 39.9rem;
    background: linear-gradient(195.45deg, rgba(255, 0, 0, 0.7) 4.61%, rgba(97, 0, 255, 0.7) 65.39%);
    border-radius: 50%;
    filter: blur(33.6rem);
    position: absolute;
    z-index: -1;
    left: -26.6rem;
  }
  &__title {
    @extend .title, .title--medium, .title--light;
  }

  &__wrapper {
    display: flex;
    margin-top: 3.7rem;
  }

  &__image-wrapper {
    align-self: flex-start;
    width: 36rem;
  }

  &__image {
    width: 100%;
    max-width: 100%;
    height: 100%;
  }

  &__list {
    list-style: none;
    margin-top: 3.6rem;
    padding: 0;
    width: 68.2rem;
  }

  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 5.4rem;
    transform: translateX(-8rem);

    &:nth-child(2) {
      transform: translateX(-1.5rem);
    }

    &::before {
      content: "";
      display: block;
      width: 3rem;
      min-width: 3rem;
      height: 3rem;
      margin-right: 7rem;
      background-image: url(../img/star.png);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;

      @extend .spin;
    }
  }

  &__item-title {
    @extend .title, .title--small, .title--light;
    padding-bottom: 0.6rem;
  }

  &__content {
    @extend .content, .content--light;
  }

  @media screen and (max-width: $laptopWidth) {
    background-image: url(../img/advantage-bg-md.png);
    background-repeat: no-repeat;
    background-position: left 20.4rem;
    background-size: 28rem;
    padding-bottom: 6rem;

    &::before {
      width: 48.6rem;
      height: 31.8rem;
      filter: blur(13.6rem);
      left: -26.6rem;
      top: 40%;
    }

    &__title {
      width: 90%;
    }
    &__wrapper {
      margin-top: 0.8rem;
    }
    &__image-wrapper {
      display: none;
    }

    &__list {
      width: 49.7rem;
      margin-left: auto;
    }

    &__item {
      transform: translateX(0);
      margin-bottom: 4rem;
      &::before {
        margin-right: 8.7rem;
        position: relative;
      }
      &:nth-child(2) {
        transform: translateX(0);
        &::before {
          left: 6.8rem;
        }
      }
    }
  }
  @media screen and (max-width: $mobileWidth) {
    padding-bottom: 7.8rem;
    background-image: none;
    &::before {
      bottom: 5rem;
      top: auto;
      left: 50%;
      transform: translateX(-50%);
      width: 30.4rem;
      height: 17.6rem;
      filter: blur(9rem);
    }

    &__wrapper {
      flex-direction: column;
    }

    &__list {
      width: 100%;
      margin-top: 2.3rem;
    }

    &__item {
      align-items: flex-start;
      &:before {
        margin-right: 0;
        width: 2rem;
        height: 2rem;
        background-size: contain;
        margin-top: 0.2rem;
      }
      &:nth-child(2) {
        &::before {
          left: 0;
        }
      }
    }
    &__image-wrapper {
      display: block;
      order: 2;
      width: 20.2rem;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

.gallery {
  padding-top: 6.1rem;
  padding-bottom: 7rem;
  background-color: $white;
  &__title {
    @extend .title, .title--medium;
  }

  &__container {
    margin-top: 4.2rem;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-flow: row dense;
  }

  &__image-wrapper {
    &:nth-child(7n + 1) {
      grid-row: span 2;
      grid-column: span 2;
    }
  }

  &__image {
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @media screen and (max-width: $laptopWidth) {
    padding-bottom: 10.2rem;
    &__container {
      margin-top: 4.2rem;
      // display: grid;
      grid-column-gap: 2rem;
      grid-row-gap: 2.5rem;
      grid-template-columns: repeat(2, 1fr);
      grid-auto-rows: 24.7rem;
      grid-auto-flow: row dense;
    }
  }
  @media screen and (max-width: $mobileWidth) {
    padding-top: 5.2rem;
    padding-bottom: 5.2rem;
    &__container {
      margin-top: 4.5rem;
      grid-row-gap: 2rem;
      grid-auto-rows: 15.4rem;
    }
  }
}

.workflow {
  padding-top: 6rem;
  padding-bottom: 2rem;
  &__title {
    @extend .title, .title--medium, .title--light;
  }

  &__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style: none;
    counter-reset: count;
    padding: 0;
    padding-bottom: 4.5rem;
    margin-top: 4.3rem;
    border-bottom: 0.1rem solid $white;
  }

  &__item {
    @extend .content, .content--light;
    width: 31%;
    display: flex;
    align-items: center;
    position: relative;
    padding-right: 0.5rem;
    margin-bottom: 3rem;

    &:not(:nth-child(3n)) {
      margin-right: 3.5rem;
    }

    &::before {
      @extend .title;
      counter-increment: count;
      content: "0" counter(count);
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $white;
      min-width: 7.8rem;
      height: 7.8rem;
      margin-right: 1.7rem;
      font-size: 4.4rem;
    }

    &--star::before {
      background-color: transparent;
      color: $white;
      font-size: 2.6rem;
      font-weight: 300;
    }
    &--star::after {
      @extend .spin;
      content: "";
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 7.8rem;
      height: 7.8rem;
      left: 0;
      top: 0;
      background-image: url(../img/star-empty.png);
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
  @media screen and (max-width: $laptopWidth) {
    padding-top: 7.9rem;
    &__list {
      padding-bottom: 3.3rem;
    }
    &__item {
      width: 50%;
      margin-bottom: 4.2rem;
      &:not(:nth-child(3n)) {
        margin-right: 0rem;
      }
    }
  }
  @media screen and (max-width: $mobileWidth) {
    padding-top: 4rem;

    &__list {
      flex-direction: column;
      margin-top: 3.2rem;
    }
    &__item {
      width: 100%;
      margin-bottom: 2.7rem;
      &::before {
        width: 5.4rem;
        min-width: 5.4rem;
        height: 5.4rem;
        font-size: 2.5rem;
        margin-right: 3.2rem;
        margin-left: 0.2rem;
      }
      &--star::before {
        font-size: 2rem;
      }
      &--star::after {
        width: 5.8rem;
        height: 5.7rem;
        min-width: 5.8rem;
      }
    }
  }
}

.reviews {
  padding-top: 4rem;
  padding-bottom: 5.5rem;
  &__wrapper {
    position: relative;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: 1fr 14rem 45.2rem;
    grid-template-areas:
      "title title images"
      "content content images"
      ". toggles images";
  }

  &__title {
    @extend .title, .title--medium, .title--light;
    grid-area: title;
  }

  &__content {
    grid-area: content;
  }

  // &__item-wrapper {
  // }

  &__item-container {
    padding-top: 6.3rem;
    padding-right: 4rem;
  }

  &__text {
    @extend .content, .content--light;
  }

  &__author {
    @extend .title, .title--small, .title--light;
    padding-top: 4.5rem;
  }

  &__images {
    grid-area: images;
    overflow: hidden;
    height: 34rem;
  }

  // &__images-container {
  // }

  &__image-wrapper {
    width: 100%;
  }

  &__image {
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__toggles {
    grid-area: toggles;
    position: absolute;
    bottom: 2.6rem;
    right: -0.1rem;
    display: flex;
  }

  &__toggle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5.6rem;
    height: 5.6rem;
    background: transparent;
    border: 0.1rem solid $white;
    color: $textContrastColor;
    cursor: pointer;
    transition: 0.3s all linear;
    &--prev {
      margin-right: 2.2rem;
    }
    &:hover {
      background: $textContrastColor;
      color: $textMainColor;
    }
  }
  @media screen and (max-width: $laptopWidth) {
    padding-top: 3.1rem;
    padding-bottom: 6rem;
    &__wrapper {
      position: relative;
      display: grid;
      grid-gap: 3rem;
      grid-template-columns: 1fr 1fr;
      grid-template-areas:
        "title  toggles"
        "images  images"
        "content  content";
    }
    &__images {
      margin-top: 1.2rem;
      height: 34.6rem;
    }
    &__toggles {
      bottom: 0;
      right: 0;
    }
    &__item-container {
      padding-top: 0;
      padding-right: 0;
    }
    &__author {
      padding-top: 3rem;
    }
  }
  @media screen and (max-width: $mobileWidth) {
    padding-top: 2rem;
    &__wrapper {
      grid-template-columns: 1fr;
      grid-gap: 2.7rem;
      grid-template-areas:
        "title"
        "images"
        "content"
        "toggles";
    }
    &__images {
      margin-top: 0;
      height: 20.6rem;
    }
    &__content {
      margin-top: 0.5rem;
    }
    &__author {
      padding-top: 2.6rem;
    }
    &__toggles {
      position: static;
      margin: 0 auto;
      margin-top: 1.1rem;
    }
    &__toggle {
      width: 5rem;
      height: 5rem;
    }
  }
}

.feedback {
  background-color: $white;
  padding-top: 6rem;
  padding-bottom: 5.8rem;
  &__container {
    display: flex;
    justify-content: space-between;
  }
  &__wrapper {
    width: 53rem;
  }

  &__title {
    @extend .title, .title--medium;
  }

  &__subtitle {
    @extend .content;
    padding-top: 1.7rem;
    padding-right: 9rem;
  }

  &__form {
    display: flex;
    flex-direction: column;
    margin-top: 2rem;
    width: 73%;
  }

  &__input {
    @extend .content, .content--large;
    width: 100%;
    border: none;
    border-bottom: 0.1rem solid $textMainColor;
    outline: none;
    padding-top: 1.3rem;
    padding-bottom: 0.9rem;
    margin-bottom: 2.2rem;
    &::placeholder {
      color: $textMainColor;
    }
    &:focus {
      border-bottom-color: rgba(0, 41, 255, 1);
    }
  }

  &__button {
    @extend .button, .button--dark;
    width: 100%;
    margin-top: 1.7rem;
  }

  &__image-container {
    width: 54.8rem;
  }

  &__image {
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @media screen and (max-width: $laptopWidth) {
    padding-top: 5.5rem;
    padding-bottom: 8rem;
    &__container {
      flex-direction: column;
    }
    &__wrapper {
      width: 100%;
    }
    &__subtitle {
      padding-right: 0;
    }
    &__form {
      width: 77%;
    }
    &__image-container {
      width: 100%;
      margin-top: 6.5rem;
      height: 39.2rem;
    }
  }
  @media screen and (max-width: $mobileWidth) {
    padding-top: 5.2rem;
    padding-bottom: 5.8rem;
    &__form {
      width: 100%;
      margin-top: 2.4rem;
    }
    &__input {
      font-size: 2.4rem;
      padding-bottom: 1.4rem;
    }
    &__button {
      margin-top: 0.7rem;
      padding-top: 1.5rem;
      padding-bottom: 1.5rem;
    }
    &__image-container {
      margin-top: 5.4rem;
      height: 20.7rem;
    }
  }
}

.footer {
  position: relative;
  overflow: hidden;
  padding-top: 5rem;
  $root: &;
  &::before {
    content: "";
    display: block;
    width: 40.7rem;
    height: 40.7rem;
    border-radius: 50%;
    background: rgba(0, 41, 255, 0.6);
    position: absolute;
    top: -26.7rem;
    left: -18.5rem;
    filter: blur(25.2rem);
  }
  &__title {
    @extend .title, .title--medium, .title--light;
  }

  &__top {
    width: 100%;
    padding-top: 2.3rem;
    padding-bottom: 5rem;
    border-bottom: 0.1rem solid $white;
    .container {
      margin: 0 auto;
    }
  }

  &__top-wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__contact {
    color: $textContrastColor;
    display: flex;
    align-items: center;
    width: 32%;
  }

  &__icon {
    width: 3.5rem;
    height: 3.5rem;
    margin-right: 2.4rem;
  }

  &__link {
    @extend .content, .content--large, .content--light;
    border-left: 0.1rem solid $white;
    padding-left: 2.4rem;
    padding-top: 1.6rem;
    padding-bottom: 1.6rem;
  }

  &__text {
    @extend #{$root}__link;
    padding-top: 0;
    padding-bottom: 0;
  }

  &__bottom {
    display: flex;
    padding-top: 1rem;
    padding-bottom: 1rem;
    justify-content: space-between;
  }

  &__bottom-content {
    @extend .content, .content--light;
    width: 26%;
    &--centered {
      text-align: center;
    }
  }

  &__bottom-link {
    @extend #{$root}__bottom-content;
  }
  @media screen and (max-width: $laptopWidth) {
    padding-top: 6.2rem;
    &::before {
      width: 33.5rem;
      height: 33.5rem;
      top: -19.7rem;
      left: auto;
      right: -13.7rem;
      filter: blur(11.2rem);
    }
    &__top {
      padding-top: 2rem;
      padding-bottom: 0.5rem;
      border: none;
    }
    &__top-wrapper {
      flex-direction: column;
      border-bottom: 0.1rem solid $white;
      padding-bottom: 2.5rem;
    }
    &__contact {
      width: 100%;
      margin-bottom: 2.3rem;
    }
    &__link,
    &__text {
      padding-top: 0.7rem;
      padding-bottom: 1rem;
      padding-left: 1.7rem;
    }
    &__text {
      br {
        display: none;
      }
    }
    &__icon {
      margin-right: 1.5rem;
    }
    &__bottom {
      flex-wrap: wrap;
      margin-bottom: 2rem;
    }
    &__bottom-content {
      width: 50%;
      &--centered {
        order: 2;
        text-align: left;
        padding-top: 1rem;
      }
    }
  }
  @media screen and (max-width: $mobileWidth) {
    padding-top: 4rem;
    &__top {
      padding-top: 3rem;
    }
    &__top-wrapper {
      padding-bottom: 0;
    }
    &__contact {
      margin-bottom: 2.8rem;
    }

    &__icon {
      width: 2.8rem;
      height: 2.8rem;
      min-width: 2.8rem;
    }
    &__link,
    &__text {
      padding-top: 0.4rem;
      padding-bottom: 0.4rem;
    }
    &__bottom {
      flex-direction: column;
      padding-top: 2.5rem;
    }
    &__bottom-content {
      width: 100%;
      font-size: 2rem;
      margin-bottom: 1.8rem;

      &--centered {
        order: 0;
        padding-top: 0;
      }
    }
  }
}
